import React, {useEffect, useState} from "react";

import {Formik, FormikHelpers} from "formik";
import {buscaLinhaParaEditar, editaLinha} from "../../domain/linha/LinhaService";
import {RouteComponentProps} from "react-router";
import {LinhaEditaFormModel} from "../../domain/linha/LinhaModels";

import AlertModel, {alertFromAxiosError} from "../../common/AlertModel";
import AppPage from "../../components/AppPage";
import {Col, Form, FormGroup, Row} from "reactstrap";
import {AppInputWithLabel} from "../../components/AppInputWithLabel";
import HorarioDaSemanaForm from "./HorarioDaSemanaForm";
import AppSaveButton from "../../components/AppSaveButton";

interface Params {
  id: string
}

const LinhaEdita: React.FC<RouteComponentProps<Params>> = ({history, match}) => {
  const id: string = match.params.id;
  const [isLoading, setIsLoading] = useState(true);
  const [alert, setAlert] = useState<AlertModel | undefined>(undefined);

  const [initialValues, setInitialValues] = useState<LinhaEditaFormModel>({
    empresaNome: "",
    rotaDestinoNome: "",
    rotaOrigemNome: "",
    segunda: [{partida: '', chegada: '',}],
    terca: [{partida: '', chegada: '',}],
    quarta: [{partida: '', chegada: '',}],
    quinta: [{partida: '', chegada: '',}],
    sexta: [{partida: '', chegada: '',}],
    sabado: [{partida: '', chegada: '',}],
    domingo: [{partida: '', chegada: '',}]
  })


  useEffect(() => {
    buscaLinhaParaEditar(id).then(response => {
      setInitialValues(response.data);
      setIsLoading(false);
    }).catch((e) => {
      setAlert(alertFromAxiosError(e))
    });
  }, [id])


  const onSubmit = (linhaFormModel: LinhaEditaFormModel, {setSubmitting}: FormikHelpers<LinhaEditaFormModel>) => {
    setAlert(undefined)
    return editaLinha(id, linhaFormModel)
      .then(() => {
        history.push(`/linhas/`)
      })
      .catch((e) => {
        setAlert(alertFromAxiosError(e));
        setSubmitting(false);
      });
  }

  return <>
    {!isLoading &&
    <LinhaEditaView {...{onSubmit, alert, initialValues}}/>}
  </>;
};


interface Props {
  initialValues: LinhaEditaFormModel
  alert?: AlertModel
  onSubmit: (formModel: LinhaEditaFormModel, helpers: FormikHelpers<LinhaEditaFormModel>) => Promise<any>
}


const LinhaEditaView: React.FC<Props> = ({initialValues, onSubmit, alert}) => {


  const header = <>Edita Linhas</>;

  return (
    <>
      <AppPage alertModel={alert} header={header}>
        <Formik<LinhaEditaFormModel>
          initialValues={initialValues}
          onSubmit={onSubmit}
        >
          {({handleSubmit, isSubmitting, values}) => {
            return (<Form onSubmit={handleSubmit}>

              <Row>
                <Col sm={12}>
                  <FormGroup>
                    <AppInputWithLabel
                      label="Empresa"
                      id={'empresaNome'}
                      name={'empresaNome'}
                      disabled={true}
                    />
                  </FormGroup>
                </Col>
              </Row>

              <RotaDaLinhaComponent/>

              <HorarioDaSemanaForm formModel={values}/>

              <AppSaveButton isSubmitting={isSubmitting}/>

            </Form>);
          }}
        </Formik>
      </AppPage>
    </>
  );
}


function RotaDaLinhaComponent() {

  return <Row>
    <Col sm={6}>
      <FormGroup>
        <AppInputWithLabel
          label="Cidade de Partida"
          id={'rotaOrigemNome'}
          disabled={true}
        />
      </FormGroup>
    </Col>

    <Col sm={6}>
      <FormGroup>
        <AppInputWithLabel
          label="Cidade de Destino"
          id={'rotaDestinoNome'}
          name={'rotaDestinoNome'}
          disabled={true}
        />
      </FormGroup>
    </Col>
  </Row>;
}


export default LinhaEdita;
