import { FormGroup, Label} from "reactstrap";
import React from "react";
import AppInput, {AppInputModel} from "./AppInput";

export interface AppInputWithLabelModel extends AppInputModel {
  label: string,
}


export function AppInputWithLabel({label, id, ...props}: AppInputWithLabelModel) {
  return (<>
    <FormGroup>
      <Label for={id} className={"label-color"}>{label}</Label>
      <AppInput {...props} id={id}/>
    </FormGroup>
  </>);

}
