import React from "react";
import {Button, Col, Row} from "reactstrap";

const AppSaveButton: React.FC<{ isSubmitting: boolean }> = ({isSubmitting}) => {
  return (
    <Row>
      <Col sm={12}><Button
        type="submit"
        color={'success'}
        className={'float-right'}
        disabled={isSubmitting}>
        Salvar
      </Button>
      </Col>
    </Row>);
}

export default AppSaveButton;
