import {
  DiaDaSemana,
  Horario,
  HorariosDaSemanaFormModel,
  LinhaCadastraFormModel
} from "../../domain/linha/LinhaModels";
import {Field, FieldArray} from "formik";
import {Button, Col, Row} from "reactstrap";
import AppInput from "../../components/AppInput";
import React from "react";


export default function HorarioDaSemanaForm({formModel}: { formModel: HorariosDaSemanaFormModel }) {

  const diasDaSemana = [
    {id: 'segunda', nome: "Segunda-Feira"},
    {id: 'terca', nome: "Terça-Feira"},
    {id: 'quarta', nome: "Quarta-Feira"},
    {id: 'quinta', nome: "Quinta-Feira"},
    {id: 'sexta', nome: "Sexta-Feira"},
    {id: 'sabado', nome: "Sábado-Feira"},
    {id: 'domingo', nome: "Domingo"},
  ];


  return (<>{diasDaSemana.map(diaDaSemana =>
    <div key={diaDaSemana.id}>
      <Row className={"mt-3"}>
        <Col sm={12}>
          <h5>{diaDaSemana.nome}</h5>
          <hr/>
        </Col>
      </Row>
      <HorarioComponent diaDaSemana={diaDaSemana} formModel={formModel}/>
    </div>
  )}</>)

}


function HorarioComponent({diaDaSemana, formModel}: { diaDaSemana: DiaDaSemana, formModel: LinhaCadastraFormModel | any }) {
  const horarios: Horario[] = formModel[diaDaSemana.id]

  return <FieldArray name={diaDaSemana.id}>
    {({insert, push, remove}) => {

      if (horarios.length === 0) {
        push({partida: '', chegada: ''})
      }

      return (<>
        {horarios.map((horario, index) =>
          <Row key={index} className={"mb-2"}>
            <Col sm={6} key={index}>
              <Row>
                <Col sm={2}>
                  <Button type={"button"}
                          color={"success"}
                          onClick={() => {
                            insert(index + 1, {partida: '', chegada: ''});
                          }}>
                    +
                  </Button>
                </Col>
                <Col sm={10}>


                  <AppInput
                    id={`${diaDaSemana.id}.${index}.partida`}
                    placeholder={"Partida"}
                    type={'time'}

                  />

                </Col>
              </Row>
            </Col>

            <Col sm={6}>
              <Row>
                <Col sm={10}>

                  <AppInput
                    id={`${diaDaSemana.id}.${index}.chegada`}
                    placeholder={"Chegada"}
                    type="time"
                    maxLength={5}
                  />

                </Col>
                <Col sm={2}>
                  <Button type={"button"} color={"danger"} onClick={() => remove(index)}>x</Button>
                </Col>
              </Row>
            </Col>
          </Row>)}

      </>)


    }}
  </FieldArray>

}
