import vouDeVanApi from "../../common/vouDeVanApi";
import {LinhaCadastraFormModel, LinhaEditaFormModel} from "./LinhaModels";
import IPage, {IPageRequest} from "../../common/IPage";
import qs from "qs";
import LinhaListagemModel from "./LinhaListagemModel";


const LINHAS = '/linhas';

export function buscaLinhasPaginadas(pageable: IPageRequest, empresaId: string) {
  return vouDeVanApi.get<IPage<LinhaListagemModel>>(LINHAS, {
    params: {
      ...pageable,
      empresaId
    }
  });
}

export function cadastraLinha(model: LinhaCadastraFormModel) {
  return vouDeVanApi.post<{ id: number }>(LINHAS, model);
}

export function editaLinha(id: string, model: LinhaEditaFormModel) {
  return vouDeVanApi.put(`${LINHAS}/${id}`, model);
}

export function buscaLinhaParaEditar(id: string) {
  return vouDeVanApi.get<LinhaEditaFormModel>(`${LINHAS}/consulta-para-editar/${id}`)
}

export function deletaLinha(linhaId: number | string) {
  const uri = LINHAS + '/' + linhaId;
  return vouDeVanApi.delete<null>(uri);
}
